import React from "react"
import { Link, graphql } from "gatsby"
import { ReactCusdis } from 'react-cusdis'
import Bio from "../components/bio"
import Layout from "../components/layout"
import SignupForm from "../components/signup-form"
import SEO from "../components/seo"
import { rhythm, scale } from "../utils/typography"

import "./blog-post.css"

class BlogPostTemplate extends React.Component {
  render() {
    const post = this.props.data.markdownRemark
    const { title, siteUrl } = this.props.data.site.siteMetadata
    const { previous, next } = this.props.pageContext
    const { location } = this.props


  let identifier = post.frontmatter.nid;
  let url = siteUrl + location.pathname;

    return (
      <Layout location={location} title={title}>
        <SEO
          title={post.frontmatter.title}
          description={post.frontmatter.description || post.excerpt}
        />
        <div className="post-container">
          <h2 style={{fontWeight: "normal"}}>{post.frontmatter.title}</h2>
          <p
            style={{
              ...scale(-1 / 5),
              display: `block`,
              marginBottom: rhythm(1),
              marginTop: rhythm(-1),
            }}
          >
            {post.frontmatter.date}
          </p>
          <div dangerouslySetInnerHTML={{ __html: post.html }} className="post-content" />

          <SignupForm />

          { 
            false && post.frontmatter.comments !== false && 
            <Bio />
          }
          { 
            false && post.frontmatter.comments !== false && 
            <ul
              style={{
                display: `flex`,
                flexWrap: `wrap`,
                justifyContent: `space-between`,
                listStyle: `none`,
                padding: 0,
                margin: 0
              }}
            >
              <li>
                {previous && (
                  <Link to={previous.fields.slug} rel="prev">
                    ← {previous.frontmatter.title}
                  </Link>
                )}
              </li>
              <li>
                {next && (
                  <Link to={next.fields.slug} rel="next">
                    {next.frontmatter.title} →
                  </Link>
                )}
              </li>
            </ul>
          }
          { 
            post.frontmatter.comments !== false && 
            <ReactCusdis
              attrs={{
                host: 'https://cusdis.com',
                appId: 'e9bc0bcb-d463-4f63-8604-34e78a1ec232',
                pageId: identifier,
                pageTitle: post.frontmatter.title,
                pageUrl: url
              }}
            />
          }
        </div>
      </Layout>
    )
  }
}

export default BlogPostTemplate

export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
        author
        siteUrl
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt(pruneLength: 160)
      html
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        comments
        nid
      }
    }
  }
`
